.Box img {
    width: 300px; 
    max-width: 100%; 
    height: 300px; 
    position: absolute; 
    object-fit: cover;
    z-index: 1; 
    display: block; 
}
.Box {
    display: inline-block; 
    position: relative; 
    width: 320px; 
    max-width: 100%; 
    height: 320px; 
    margin: 0; 
}
.Overlay{
    background: rgba(0, 0, 0, 0); 
    width: 300px; 
    max-width: 100%; 
    height: 300px; 
    position: absolute; 
    margin: auto;
    z-index: 2; 
    text-align: center; 
    transition: .3s; 
    opacity: 0; 
    line-height: 300px; 
    text-transform: uppercase; 
}

.Overlay:hover{
    background: rgba(0, 0, 0, .8); 
    transition: .3s; 
    opacity: 1;
    color: white; 
    font-style: normal;   
}

.CaptionOverlay{
    background: rgba(0, 0, 0, .6); 
    width: 300px; 
    height: 50px; 
    position: absolute; 
    margin: auto;
    z-index: 2; 
    text-align: center; 
    line-height: 50px; 
    text-transform: uppercase; 
    bottom:20px;
    color: white;
}

